<template>
  <div class="moneyadd" :style="item">
      <Goback title="提现"></Goback>
      <div class="moneyadd_text">请绑定持卡人本人的银行卡</div>
      <div class="moneyadd_item">
          <div class="item_children">
              <div class="children_box">
                  <span>持卡人</span>
                  <input class="box_input" v-model="username" type="text" placeholder="请输入持卡人姓名" />
              </div>
              <div class="children_box">
                  <span>卡号</span>
                  <input class="box_input" v-model="usercardNumber" type="text" placeholder="请输入卡号" />
              </div>
              <div class="children_box">
                  <span>手机号</span>
                  <input class="box_input" v-model="userphone" type="text" placeholder="请输入银行预留手机号" />
              </div>
              <div class="children_box navigator_arrow" @click="()=>this.show=true">
                  <span>银行名称</span>
                  <input class="box_input box_r" disabled :value="bankName" type="text" placeholder="请选择银行名称" />
              </div>
          </div>
          <div class="item_btn" :class="_setnull?'':'btn_null'">保存</div>
      </div>
      <van-popup v-model="show" round  position="bottom">
          <div class="moneyadd_pop">
              <div class="pop_item" 
              v-for="(item,index) in bankView"
              :key="index"
              @click="_changeBank(item)"
              >{{item.name}}</div>
          </div>
      </van-popup>
  </div>
</template>

<script>
export default {
    name:'moneyadd',
    data(){
        return{
            username:'',//持卡人姓名
            usercardNumber:'',//卡号
            userphone:'',//手机号
            bankName:'',
            show:false,
            item:{height:'',width:''},
            bankView:[
                {id:1,name:'中国工商银行'},
                {id:2,name:'中国农业银行'},
                {id:3,name:'中国招商银行'},
                {id:4,name:'中国建设银行'},
                {id:5,name:'中国银行'},
                {id:6,name:'交通银行'},
            ]
        }
    },
    created(){
        this.item = {
            height: document.documentElement.clientHeight + "px",
            width: document.documentElement.clientWidth + "px"
        };
    },
    computed:{
        _setnull:function(){
            const {username,usercardNumber,userphone,bankName} = this
            return username.length&&usercardNumber.length&&userphone.length&&bankName.length
        }
    },
    methods:{
        _changeBank(view){
            this.bankName=view.name
            this.show=false
        }
    }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


